import { Button, Col, Container, Form, Row } from "react-bootstrap";
import TopBanner from "../components/TopBanner";
import Footer from "../containers/Footer";
import Headers from "../containers/Header";
import "./Signup.css";

const Signup = () => {
  return (
    <>
      <Headers></Headers>
      <TopBanner content="NG K TI KHON" />
      <Container>
        <Row className="justify-content-center signup-margin">
          <Col className="col-sm-12 col-xs-12 col-md-8">
            <div className="col-md-offset-3">
              <div className="title_style3  mt-5 pt-3">
                <h3>NG K TI KHON</h3>
              </div>
              <div className="box-cont">
                <Form>
                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalName"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Tn ng nhp
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        placeholder="Tn ng nhp"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalFullname"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      H v tn
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        placeholder="H v tn"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalPassword"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Mt khu
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        type="password"
                        placeholder="Mt khu"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalRepeatPassword"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Nhp li mt khu
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        type="password"
                        placeholder="Nhp li mt khu"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalEmail"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Email
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        type="email"
                        placeholder="Email"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalPhone"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      S in thoi
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        placeholder="S in thoi"
                      />
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalPhone"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Tnh thnh
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Select
                        aria-label="Default select example"
                        className="signup-button-padding"
                      >
                        <option>-- Tnh thnh --</option>
                        <option value="1">H Ni</option>
                        <option value="2">H Giang</option>
                        <option value="3">H Ch Minh</option>
                      </Form.Select>
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalPhone"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      Qun huyn
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Select
                        aria-label="Default select example"
                        className="signup-button-padding"
                      >
                        <option>-- Qun huyn --</option>
                        <option value="1">Nam T Lim</option>
                        <option value="2">Qun 5</option>
                        <option value="3">Qun 3</option>
                      </Form.Select>
                    </Col>
                  </Form.Group>

                  <Form.Group
                    as={Row}
                    className="mb-3"
                    controlId="formHorizontalAddress"
                  >
                    <Form.Label column sm={3} className="signup-control-lable">
                      a ch
                    </Form.Label>
                    <Col sm={9}>
                      <Form.Control
                        className="signup-button-padding"
                        placeholder="a ch"
                      />
                    </Col>
                  </Form.Group>
                </Form>
                <div className="text-center">
                  <Button
                    variant="warning"
                    type="submit"
                    className="signup-btn-submit me-2"
                  >
                    <i className="fa-regular fa-circle-check"></i>&nbsp; ng k
                  </Button>
                  <Button
                    variant="warning"
                    type="submit"
                    className="signup-btn-submit ms-2"
                  >
                    <i className="fa-solid fa-xmark"></i>&nbsp; Hy b
                  </Button>
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
      <Footer></Footer>
    </>
  );
};

export default Signup;
