import { Col, Container, Form, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import MenuManagement from "../components/MenuManagement";
import TopBanner from "../components/TopBanner";
import "./ManagerAccount.css";

const AddressAdd = () => {
  return (
    <>
      <TopBanner content="QUN L TI KHON" />
      <Container fluid>
        <Row className="justify-content-center manaccount-margin">
          <Col className="col-sm-12 col-xs-12 col-md-8">
            <Row>
              <Col className="col-md-3">
                <MenuManagement></MenuManagement>
              </Col>
              <Col className="col-md-9 mt-5 pt-3">
                <div className="user-bg-color">
                  <div className="title_style4 d-flex justify-content-between">
                    <h4>
                      <i className="fa-regular fa-address-book me-2"></i>S A CH
                    </h4>
                  </div>
                  <Form>
                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalName"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        H v Tn
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Control
                          className=""
                          placeholder="H Th Tng Vy"
                        />
                      </Col>
                    </Form.Group>

                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalName"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        S in thoi
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Control
                          className=""
                          placeholder="S in thoi"
                        />
                      </Col>
                    </Form.Group>

                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalName"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        a ch
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Control className="" placeholder="a ch" />
                      </Col>
                    </Form.Group>

                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalPhone"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        Tnh thnh
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Select
                          aria-label="Default select example"
                          className=""
                        >
                          <option>-- Tnh thnh --</option>
                          <option value="1">H Ni</option>
                          <option value="2">H Giang</option>
                          <option value="3">H Ch Minh</option>
                        </Form.Select>
                      </Col>
                    </Form.Group>

                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalPhone"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        Qun huyn
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Select
                          aria-label="Default select example"
                          className=""
                        >
                          <option>-- Qun huyn --</option>
                          <option value="1">Nam T Lim</option>
                          <option value="2">Qun 5</option>
                          <option value="3">Qun 3</option>
                        </Form.Select>
                      </Col>
                    </Form.Group>

                    <Form.Group
                      as={Row}
                      className="mb-3 justify-content-center"
                      controlId="formHorizontalAddress"
                    >
                      <Form.Label column sm={2} className="fw-bold">
                        a ch nh
                      </Form.Label>
                      <Col sm={6}>
                        <Form.Control className="" placeholder="a ch nh" />
                      </Col>
                    </Form.Group>
                  </Form>
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default AddressAdd;
